<template>
  <div class="page">
    <landing :section="section" />
    <section class="gallery">
      <figure v-for="project in section.projects" :key="project.name">
        <img :src="project.img" />
        <button>Download Itinerary</button>
      </figure>
      <h3 v-if="section.projects.length == 0">No Projects here yet</h3>
    </section>
    <f-footer />
  </div>
</template>

<script>
import landing from "../components/landing.vue";
import FFooter from "../components/FFooter.vue";

export default {
  name: "Web",
  components: { landing, FFooter },
  data() {
    return {
      section: this.$store.state.sections.find(
        (section) => section.name == "Website Development"
      ),
    };
  },
};
</script>
